body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    background-color: #009933;
    color: white;
    width: 100%;
}

.instruction-header {
    background-color: #ffcc33;
    color: white;
    width: 100%;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}

.game-container {
    text-align:center;
    margin-top: 2rem;
}

.game-square {
    margin: 1rem;
    display: inline-block;
}
.footer {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    background-color: #009933;
    color: white;
    width: 100%;
    position: fixed;
    bottom: 0;
}
